import moment from 'moment';
import 'moment/locale/uk';
import Day from './Day/Day';
import './dateTimePicker.css';
import { useLayoutEffect, useMemo, useState } from 'react';
import { ReactComponent as RowBotton } from '../../icons/rowbotton.svg';

moment.locale('uk');


const DateTimePicker = () => {
  const activeDatas = useMemo(() => retuernFinishDate(), []);
  const [nShift, setNShift] = useState(0);
  const dayOfYear = moment().dayOfYear() - 1;
  // const [activeDatas, setActiveData] = useState(memoizedState);
  console.log(activeDatas)
  return (
    <div className='dateTimePicker'>
      <h4 className='dateTimePicker__title'>Забронювати час</h4>
      <div className="dateTimePicker_header">
        <RowBotton className="dateTimePicker__arowLeft" onClick={() => { setNShift(nShift - 1) }} />
        <span className="dateTimePicker__year">{activeDatas[dayOfYear + nShift].date.format("MMMM, YYYY")}</span>
        <RowBotton className="dateTimePicker__arowRight" onClick={() => { setNShift(nShift + 1) }} />
      </div>
      <div className="dateTimePicker_schedule">
        <Day key={dayOfYear} data={activeDatas[dayOfYear + nShift]} />
        <Day key={dayOfYear + 1} data={activeDatas[dayOfYear + nShift + 1]} />
        <Day key={dayOfYear + 2} data={activeDatas[dayOfYear + nShift + 2]} />
        <Day key={dayOfYear + 3} data={activeDatas[dayOfYear + nShift + 3]} />
      </div>
    </div>
  );
};

const retuernFinishDate = () => {
  console.log(1)
  const activeData = {};
  const year2023 = generateTimeOptionsYear(moment(), moment().add(1, 'years'));
  year2023.forEach((day, index) => activeData[index] = generateTimeOptions(day));
  return activeData
}
const generateTimeOptions = (selectedDate) => {

  const start = moment(selectedDate).startOf('day').add(10, 'hours');
  const end = moment(selectedDate).startOf('day').add(19.5, 'hours');
  const options = [];
  while (start.isBefore(end)) {
    options.push({ time: start.format('HH:mm'), reservation: false });
    start.add(30, 'minutes');
  }
  return { date: selectedDate, schedule: options };
};

const generateTimeOptionsYear = (startDateStr, endDateStr) => {
  // Встановлення початкової дати
  // const startDate = moment(startDateStr);

  // Встановлення кінцевої дати
  const endDate = moment(endDateStr);

  // Створення порожнього об'єкта дат Moment
  const allDays = [];

  // Цикл для проходження крізь усі дати між початковою та кінцевою датами
  for (var d = startDateStr; d <= endDate; d.add(1, 'days')) {
    // Створення об'єкта Moment для поточної дати
    var currentDay = moment(d);

    allDays.push(currentDay);
    // Додавання об'єкта Moment до об'єкту дат Moment
    // allDays[currentDay.format('YYYY-MM-DD')] = currentDay;
  }

  return allDays;

}

export default DateTimePicker;