import React, { useReducer } from 'react'
import Slot from './Slot/Slot';

import './day.css';

function Day(props) {
  const { data, setSelectedDate } = props;
  const [, forceUpdate] = useReducer(x => x + 1, 0)
  return (
    <div className='dateTimePicker_day'>
      <div className='Day__wrapperDay'>
        <span className='Day__blockDayName'>{data.date.format('dd')}</span>
        <span>{data.date.format('DD')}</span>
      </div>
      {data.schedule.map(day => { return <Slot key={day.time} onClick={() => { day.busy = !day.busy; forceUpdate(); }} busy={day.busy} time={day.time}></Slot> })}
    </div>)
}

export default React.memo(Day)